.header__show-hide-menu--hide-menu {
  background-image: url(/payapi/static/media/close.825df35a.svg);
}

.header__link-list-mobile {
  padding-top: 7rem;
  display: flex;
  flex-direction: column;
}

.header__link-mobile {
  margin-bottom: 1rem;
}

.header__nav-mobile {
  position: absolute;
  height: 100vh;
  background-color: var(--mirage-blue);
  top: 0;
  right: 0;
  color: var(--gray);
  font-weight: bold;
  width: 70vw;
  text-align: center;
  z-index: 100;
  overflow: hidden;
}

.header__nav-mobile * {
  z-index: 10;
}

.header__nav-mobile::before {
  content: "";
  height: 1px;
  width: calc(100% - (var(--m-page-margin-x) * 2));
  background-color: var(--smoke-gray);
  position: absolute;
  top: 80px;
  left: 50%;
  z-index: 10;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}

.header__nav-mobile::after {
  border-radius: 9999px;
  content: "";
  display: block;
  height: 780px;
  width: 780px;
  position: absolute;
  z-index: 0;
  background-color: var(--light-mirage-blue);
  /* responsive center elem */
  left: 28%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  top: -550px;
}

.header__schedule-button-mobile {
  margin-top: 0.5rem;
}

.header__nav {
  display: none;
}

.header__link-mobile:hover {
  color: var(--primary-white);
}

@media screen and (min-width: 768px) {
  .header {
    position: relative;
    z-index: 10;
  }

  .header__nav-mobile {
    display: none;
  }

  .header__show-hide-menu {
    display: none;
  }

  .header__nav {
    flex: 1 1 0%;
    display: flex;
  }

  .header__link-list {
    margin-left: auto;
    margin-right: auto;
  }

  .header__link:hover {
    color: var(--blue);
  }

  .header__nav li {
    margin-left: 1.25rem;
    margin-right: 1.25rem;
    font-weight: 700;
    color: var(--dark-blue)
  }

  .footer__content-wrapper {
    display: flex;
    align-items: center;
  }

  .nav-bottom {
    margin-left: auto;
    margin-right: auto;
  }

  .nav-bottom>ul {
    display: flex;
  }

  .social-media {
    margin-left: auto;
  }

  footer::after {
    left: 400px;
    -webkit-transform: none;
    transform: none;
    -webkit-transform: initial;
    transform: initial;
    top: -100px;
  }
}

@media screen and (min-width: 1248px) {
  header {
    margin-left: auto;
    margin-right: auto;
    max-width: var(--max-width-home-desktop);
  }

  header.page-margin-x {
    margin-left: auto;
    margin-right: auto;
  }

  .header__link-list, .nav-bottom {
    margin-left: 70px;
    margin-right: auto;
  }

  .footer__content-wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: var(--max-width-home-desktop);
  }

  footer::after {
    left: auto;
    left: initial;
    right: -130px;
  }
}

