

.info-about-us {
    @apply mt-10 text-center;
}
.info-about-us__heading {
    @apply my-4;
    font-family: var(--heading-font-family);
    color: var(--blue);
    font-size: var(--m-info-about-us__heading--font-size);
}
.info-about-us__text {
    color: var(--light-blue);
}

.statistics-about-us {
    @apply my-6;
    border-top: 1px solid #BFCBD5;
    border-bottom: 1px solid #BFCBD5;
}

.statistics-about-us__heading {
    font-size: var(--m-statistics-about-us__heading--font-size);
}

.statistics-about-us__number {
    @apply leading-tight;
    font-size: var(--m-statistics-about-us__number--font-size);
    font-family: var(--heading-font-family);
    color: var(--primary-pink);
    display: inline-block;    
}

.statistics-about-us__info-wrapper {
    @apply flex flex-col justify-center items-center my-8;
}

.about-page__image-container {
    @apply mt-32 mb-14;
    width: 100%;
    height: 300px;
    background-image: url("./assets/about/mobile/image-team-members.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
@media screen and (min-width: 768px) {
    .page-heading--about-page {
        @apply mx-auto;
        max-width: 580px;
    }

    .info-about-us {
        @apply mt-14 flex flex-row text-left;
    }

    .info-about-us__heading {
        @apply my-0 leading-none;
        font-family: var(--heading-font-family);
        color: var(--blue);
        font-size: var(--t-info-about-us__heading--font-size);
    }

    .info-about-us__text {
        @apply ml-auto;
        color: var(--light-blue);
        max-width: 456px;
    }

    .statistics-about-us {
        @apply my-6;
        border: none;   
    }

    .statistics-about-us {
        @apply flex flex-row;
    }
    
    .statistics-about-us__info-wrapper {
        @apply flex-1 items-start py-8 my-0 mr-2;
        border-top: 1px solid #BFCBD5;
        border-bottom: 1px solid #BFCBD5;
    }

    .about-page__image-container {
        background-image: url("./assets/about/tablet/image-team-members.jpg");
    }
}
@media screen and (min-width: 1000px) {
    .page-heading--about-page {
        max-width: var(--d-about-page-section-max-width);
        padding-right: 100px; 
    }
    .info-about-us {
        @apply mx-auto;
        max-width: var(--d-about-page-section-max-width);
    }
    
}

@media screen and (min-width: 1248px) {
    .page-heading--about-page {
        @apply ml-auto;
    }
    .about-page__image-container{
        background-image: url('./assets//about/desktop/image-team-members.jpg');
        height: 450px;
        position: relative;
    }
    .about-page__image-container::after {
        @apply rounded-full;
        content: "";
        display: block;
        height: 780px;
        width: 780px;
        
        position: absolute;
        z-index: -1;
        background-color: var(--lighter-blue);
        top: 50%;
        transform: translate(0%, -50%);
        left: -560px;
    }
}